body{
    height: 100vh;
    font-family: 'Roboter' ,var(--fontstack);
}

#content {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* header styling */

header{
    height: 95px;
    background-color: var(--boldRed);
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.4);
    display: grid;
    grid-template-columns: 1fr repeat(3, 1fr) 6fr;
    grid-auto-flow: column;
    align-items: center;
    flex: 0 0 auto;
}

.header,
.home{
    color: white;
}

.logo {
    display: flex;
    align-items: center;
}

.name{
    font-size: 3.3rem;
    font-weight: 800;
    font-style: italic;
    margin-right: 5px;
}

.icon {
    width: 75px;
    height: 75px;
}

.navBttn {
    height: 90%;
    font-size: 1.3rem;
    background-color: var(--boldRed);
    border: none;
    border-left: 1px solid white;
}

.navBttn:hover {
    background-color: var(--darkRed);
}

/* home page styling */

.home-container {
    flex: 1 0 auto;
    margin-top: 50px;
    padding-bottom: 50px;
}

.home-content {
    height: 960px;
    width: 640px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
    background-size: cover;
    margin: 0 auto 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.home-text,
.announcement {
    width: 100%;
    background-color: rgba(0, 0, 0, 0.35);
    padding: 10px 0px;
}

.title,
.message,
.ann-mssg {
    width: 90%;
    text-align: center;
    margin-right: auto;
    margin-left: auto;
}

.title {
    font-size: 2.7rem;
    font-weight: 700;
    margin-bottom: 30px;
}

.message {
    font-size: 1.4rem;
    line-height: 1.5;
    margin-bottom: 10px;
}

.ann-mssg {
    font-size: 3rem;
    font-style: italic;
    border: 2px dashed white;
    border-radius: 15px;
}

/* contact page styling */

.contact-container {
    width: 70%;
    margin-top: 50px;
    padding-bottom: 50px;
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-gap: 20px;
    flex: 1 0 auto;
}

.hours-info {
    grid-column: 1;
    grid-row: 1;
}

.locations-info {
    grid-column: 1;
    grid-row: 2;
}

.con-info {
    grid-column: 2;
    grid-row: 1/3;
}

.container {
    border: 3px solid black;
    border-radius: 15px;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.4);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.heading {
    font-size: 1.7rem;
    font-weight: bold;
}

.small-heading {
    font-size: 1.1rem;
    font-weight: 600;
}

.container > p,
.container > .heading,
.container > div,
.container > li {
    margin-left: 10px;
}

/* menu page styling */

.menu-page-container {
    margin-top: 50px;
    padding-bottom: 50px;
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.menu-title {
    background-color: var(--boldRed);
    color: white;
    font-size: 2.8rem;
    font-weight: bold;
    font-style: italic;
    margin-bottom: 35px;
    padding: 0px 10px;
    flex: 1 0 auto;
}

.menu-items-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-gap: 45px;
    flex: 1 0 auto;
}

.menu-title,
.menu-item {
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.4);
}

.menu-item {
    border: 1px solid black;
    display: flex;
    flex-direction: column;
}

.food-image {
    height: 300px;
    width: 300px;
    flex: 0 0 auto;
}

.food-name {
    background-color: var(--boldRed);
    color: white;
    text-align: center;
    font-size: 1.3rem;
    font-weight: bold;
    flex: 1 1 auto;
